<template>
  <div>
    <el-row>
      <el-col>
        <el-table :data="articles" style="width: 100%">
          <el-table-column label="文章ID" prop="articleID" v-if="show = false"></el-table-column>
          <el-table-column label="标题" prop="title"></el-table-column>
          <el-table-column label="发布人" prop="staffName"></el-table-column>
          <el-table-column label="所属部门" prop="depName"></el-table-column>
          <el-table-column align="right">
            <template slot="header">
              <el-button size="small" type="primary" @click="post()">
                <i class="el-icon-circle-plus-outline" />添加文章
              </el-button>
            </template>
            <template slot-scope="scope">
              <el-button size="small" type="info" @click="select(scope.row)" round>
                <i class="el-icon-search" />查看
              </el-button>
              <el-button size="small" type="danger" @click="Delete(scope.row)" round>
                <i class="el-icon-delete-solid" />删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row>
      <el-col>
        <el-pagination
          background
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
        ></el-pagination>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Qs from "qs";
export default {
  data() {
    return {
      currentPage: 1,
      total: 0,
      articles: []
    };
  },
  methods: {
    post() {
      this.$router.push({
        name: "NewArticle"
      });
    },
    select(row) {
      sessionStorage.setItem('articleID', row.articleID)
      this.$router.push({
        name: "articleDetail",
        params: { articleID: row.articleID }
      });
    },
    Delete(row) {
      console.log("delete对象：" + row);
      var api = "http://localhost:8888/article/" + row.articleID;
      this.axios
        .delete(api)
        .then(response => {
          console.log(response);
          this.$message({
            showClose: true,
            message: "操作成功",
            type: "success"
          });
          this.reload();
        })
        .catch(error => {
          console.log(error);
        });
    },
    handleCurrentChange(current) {
      this.currentPage = current;
      var api = "http://localhost:8888/article/" + current + "/" + 5;
      this.axios
        .get(api)
        .then(response => {
          console.log("分页响应对象：" + response);
          this.articles = response.data.records;
          this.total = response.data.total;
        })
        .catch(error => {
          console.log(error);
        });
    },
    getData() {
      var api =
        "http://localhost:8888/article/" +
        this.$route.params.current +
        "/" +
        this.$route.params.size;
      this.axios
        .get(api)
        .then(response => {
          console.log(response);
          this.articles = response.data.records;
          this.total = response.data.total;
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  created() {
    this.getData();
  }
};
</script>

<style lang="scss" scoped>
</style>